<template>

    <div class="col-lg-12">

        <div class="heading-border mb-3" v-if="Formtype == 'Create'">
            <h5 class="h5">
                <span class="d-md-inline-block">

                    <div class="custom-control custom-checkbox">
                        <input type="checkbox" class="isRecurring custom-control-input" id="chkAll_11"
                            v-model='recurring' @click="changeRecurring($event)" name="checkbox" />
                        <label class="custom-control-label universal-custom-control-label pt-1" for="chkAll_11"></label>
                    </div>
                    {{$t('Recurring') }}
                </span>
            </h5>
        </div>

        <dynamic-form lang="en" ref="invoiceFields" :buttons="false" :schema="FormSchema">

            <template v-slot:tgslot-Invoice_Number="{ data }">
                <!--  -->
                <Field name="Invoice_Number" :rules="{ 'required': false }" v-slot="{ errors }">
                    <!-- :disabled="isDraftMode ? false : true" -->
                    <!-- 'bg-white': isDraftMode === false ? true : false -->
                    <div class="input-group bg-white">
                        <input type="text" id="" v-model="data.fieldInfo.value" v-bind:class="{
                            'form-control': true,
                            'is-invalid': errors[0] || false,

                        }" :disabled="true" />
                    </div>

                    <small v-if="Formtype == 'Create'"><a v-if="isHideMessage == false" class="text-primary"
                            v-on:click="toggleMode">
                            {{ modeText }}
                        </a></small>
                    <span else class="text-primary">
                        <!-- <span v-if="errors[0]" name="Invoice Number" class="invalid-feedback d-block">{{
                            errors[0] }}</span> -->
                    </span>
                </Field>
            </template>

            <template v-slot:tgslot-Project="{ data }">
                <div class="input-group">
                    <MutliSelectDropDown :options="filteredOptions" :value="selectedProjects"
                        placeholder="Select Project" :searchable="true" searchPlaceholder="Search Project" :maxItems="3"
                        :selectAllEnabled="true" allText="Select All" @input="updateSelectedProjects"
                        :Formtype="Formtype" />
                </div>
            </template>

            <template v-slot:tgslot-Template="{ data }">

                <Field name="Template" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                    <div class="form-group">
                        <div class="input-group mb-3">
                            <select name="Template" v-model="data.fieldInfo.value"
                                @change="handleTemplateChange(data.fieldInfo.value)"
                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                <option value="">{{ $t('Select') }}</option>
                                <option v-for="(item, itemIndex) in templateList" :key="itemIndex" :value="item.value">
                                    {{ item.name }}</option>
                            </select>
                            <div class="input-group-append">
                                <span class="input-group-text"><a id="linkPreviewtemplate"
                                        v-on:click="previewTemplate()" class="text-dark" href="javascript:;"
                                        data-toggle="tooltip" title="" data-toggle-tooltip="tooltip"
                                        data-original-title="View Template"><i class="fa fa-eye"></i></a></span>
                            </div>
                            <span v-if="errors[0]" name="Template" class="invalid-feedback d-block">{{
                                errors[0] }}</span>
                        </div>

                    </div>

                </Field>

            </template>

        </dynamic-form>

        <TemplateReview v-if="isViewTemplate" :PreviewTemplate="previewTemplateContent"></TemplateReview>

    </div>

</template>
<script>
import DataService from '../../../services/DataService';
import MutliSelectDropDown from './MutliSelectDropDown.vue';
import TemplateReview from './TemplatePreview.vue';
export default {
    components: {
        MutliSelectDropDown, TemplateReview
    },
    computed: {
        checkMode() {
            return this.isDraftMode == false ? false : true;
        },
        labelText() {
            return this.isDraftMode ? "Invoice Number" : " Bill Status";
        },

        modeText() {
            return this.isDraftMode
                ? "Switch to Draft Mode"
                : "Click to generate Invoice Number";
        },

    },
    props: {
        InvoiceFieldsDetails: {
            type: Object,

        },
        Formtype: {
            type: String
        }
    },

    watch: {
        InvoiceFieldsDetails(newValue, oldValue) {
            //
            this.fieldsDetails();
        },

    },
    data() {

        return {
            isDraftMode: false,
            Bill_To: '',
            recurring: false,
            selectedProjects: [],
            isHideMessage: false,
            filteredOptions: [],
            templateList: [],
            TemplateId: '',
            isViewTemplate: false,
            previewTemplateContent: '',
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('BillTo'),
                            name: "Bill_To",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.projectListByClientId
                            },

                            placeholder: this.$t('Bill To'),
                            validationRules: { "required": true, max: 100 },
                            disabled: false,
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('InvoiceStatus'),
                            name: "Invoice_Number",
                            value: "Draft Mode",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('InvoiceStatus'),
                            // validationRules: { "required": this.checkMode, max: 100 },
                            validationRules: { "required": false, max: 100 },
                            disabled: false,
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Project'),
                            name: "Project",
                            value: "",
                            mode: "tag",
                            config: {
                                options: [],
                                showAddIcon: false,
                                // onAddButtonClick: this.OpenAddTagPopup
                            },
                            placeholder: this.$t('Project'),
                            validationRules: { "required": false, max: 100 },
                            disabled: false,
                        },

                        {
                            astype: "DateField",
                            label: this.$t('DateFrom'),
                            name: "Date_From",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                // onChange: this.InvoiceComponentFieldsValues,
                                onChange: this.setDateForm,
                                minDate: ''
                                // maxDate: false
                            },
                            validationRules: { "required": true },
                            disabled: false,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('DateTo'),
                            name: "Date_To",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                onChange: this.InvoiceComponentFieldsValues,
                                // maxDate: false
                                minDate: ''
                            },
                            validationRules: { "required": true },
                            disabled: false,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('InvoiceBillIssueDate'),
                            name: "Issue_Date",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                onChange: this.InvoiceComponentFieldsValues,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                            disabled: false,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('DueDate'),
                            name: "Due_Date",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                onChange: this.InvoiceComponentFieldsValues,
                                // maxDate: false
                            },
                            validationRules: { "required": true },
                            disabled: false,
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Template'),
                            name: "Template",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.InvoiceComponentFieldsValues,
                            },
                            placeholder: this.$t('Template'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: "Due Date Comment",
                            name: "Due_Date_Comment",
                            value: "",
                            placeholder: "",
                            config: {
                                onChange: this.InvoiceComponentFieldsValues,
                            },
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: "Terms",
                            name: "Terms",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            config: {
                                onChange: this.InvoiceComponentFieldsValues,
                            },
                        }

                    ]
                },
            ],

            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    value: 'Y',
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    // onClick: this.GenerateBatch
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    // onClick: this.Close
                }
            ],
            currencyId: null,
            currencyName: '',
            IsSharedCompany: false
        }
    },
    async created() {
       // debugger;
        await this.getClients()
        await this.getTemplates();
        await this.getInitialData();


    },
    methods: {


        setDateForm(data, event) {
            debugger;
            //this.FormSchema[0].Data[4].config.minDate = data;
             
              const datetoField = this.FormSchema[0].Data.find(f => f.name === "Date_To");
              datetoField.config.minDate = data;
            //  this.FormSchema[2].Data[2].config.minDate = data
            // if (this.FormSchema[2].Data[0].value >= this.FormSchema[2].Data[1].value) {
            //     this.FormSchema[2].Data[1].value = '';
            // } else {
            //     //
            // }
            this.InvoiceComponentFieldsValues();
        },
        async fieldsDetails() {
            //debugger;

            

             const billToField = this.FormSchema[0].Data.find(f => f.name === "Bill_To");
           if (billToField) {
           billToField.value = this.InvoiceFieldsDetails.bill_to;
           billToField.disabled=true;
            }


             this.isDraftMode = true;
            let label = this.isDraftMode ? "Invoice Number" : "Bill Status";
            
            //this.FormSchema[0].Data[1].label = label;

               

               const invoicenumberField = this.FormSchema[0].Data.find(f => f.name === "Invoice_Number");
              invoicenumberField.label = label;
            invoicenumberField.value = this.InvoiceFieldsDetails.bill_number;
            invoicenumberField.disabled = true;
            


            const templateField = this.FormSchema[0].Data.find(f => f.name === "Template");
           if (templateField) {
           templateField.value = this.InvoiceFieldsDetails.template_id;
            }
        const DateFromField = this.FormSchema[0].Data.find(f => f.name === "Date_From");
           if (DateFromField) {
           DateFromField.value = this.InvoiceFieldsDetails.BillDate_From;
           DateFromField.disabled=true;
            }
        const BillDateToField = this.FormSchema[0].Data.find(f => f.name === "Date_To");
           if (BillDateToField) {
           BillDateToField.value = this.InvoiceFieldsDetails.BillDate_To;
           BillDateToField.disabled=true;
            }

            const billissuedateField = this.FormSchema[0].Data.find(f => f.name === "Issue_Date");
           if (billissuedateField) {
           billissuedateField.value = this.InvoiceFieldsDetails.bill_issue_date;
           billissuedateField.disabled=true;
            }

            const billduedateField = this.FormSchema[0].Data.find(f => f.name === "Due_Date");
           if (billduedateField) {
           billduedateField.value = this.InvoiceFieldsDetails.bill_due_date;
           //billduedateField.disabled=true;
            }

            const billduedatecommentField = this.FormSchema[0].Data.find(f => f.name === "Due_Date_Comment");
           if (billduedatecommentField) {
            billduedatecommentField.value = this.InvoiceFieldsDetails.bill_due_date_comment;
              this.currencyId = this.InvoiceFieldsDetails.CurrencyId;
            this.Bill_To = this.InvoiceFieldsDetails.bill_to

            }
             const TermsField = this.FormSchema[0].Data.find(f => f.name === "Terms");
 if (TermsField)
  {
    TermsField.value = this.InvoiceFieldsDetails.Terms;
  }
            debugger;
            const projectField = this.FormSchema[0].Data.find(f => f.name === "Project");
            //projectField.disabled=false;
            await this.projectListByClientId('', '',  billToField.value);
             //await this.projectListByClientId('', '',  billToField.value);
            let projectIdsArray = this.InvoiceFieldsDetails.projectIds.split(',');
            let selectedValues = this.filteredOptions.filter(option => projectIdsArray.includes(option.value));
            this.selectedProjects = selectedValues;
            
            //this.FormSchema[0].Data[0].value = this.InvoiceFieldsDetails.bill_to
            //this.FormSchema[0].Data[0].disabled = true;
            
           // await this.projectListByClientId('', '',  billToField.value);
            //this.FormSchema[0].Data[1].value = this.InvoiceFieldsDetails.bill_number;
           // this.FormSchema[0].Data[1].disabled = true;
           // let projectIdsArray = this.InvoiceFieldsDetails.projectIds.split(',');
           // let selectedValues = this.filteredOptions.filter(option => projectIdsArray.includes(option.value));
            //this.selectedProjects = selectedValues;
            //this.FormSchema[0].Data[3].value = this.InvoiceFieldsDetails.BillDate_From
            //this.FormSchema[0].Data[3].disabled = true;
            //this.FormSchema[0].Data[4].value = this.InvoiceFieldsDetails.BillDate_To
            //this.FormSchema[0].Data[4].disabled = true;
            //this.FormSchema[0].Data[5].value = this.InvoiceFieldsDetails.bill_issue_date
            //this.FormSchema[0].Data[5].disabled = true;
            //this.FormSchema[0].Data[6].value = this.InvoiceFieldsDetails.bill_due_date
            //this.FormSchema[0].Data[6].disabled = true;
            //this.FormSchema[0].Data.find(f => f.name === "bill_due_date_comment").value = this.InvoiceFieldsDetails.template_id
            //this.FormSchema[0].Data[8].value = this.InvoiceFieldsDetails.bill_due_date_comment
           // this.FormSchema[0].Data[9].value = this.InvoiceFieldsDetails.Terms
            //this.currencyId = this.InvoiceFieldsDetails.CurrencyId;
           // this.Bill_To = this.InvoiceFieldsDetails.bill_to
        },

        toggleMode() {
            this.isDraftMode = !this.isDraftMode;
            if (this.isDraftMode == true) {
                this.getLatestInvoiceNumber();
            }
            this.FormSchema[0].Data[1].value = this.isDraftMode ? '' : 'Draft Mode';
            const label = this.isDraftMode ? "Invoice Number" : "Bill Status";
            return this.FormSchema[0].Data[1].label = label;
        },
        updateSelectedProjects(newSelection, singleSelectDetails) {
            debugger;
            let [projectId, currencyId, currencyName] = singleSelectDetails.value.split('^');
            this.currencyId = currencyId;
            this.currencyName = currencyName;

            console.log(projectId);
            console.log(currencyId);
            console.log(currencyName);

            this.selectedProjects = newSelection;
            this.InvoiceComponentFieldsValues();
        },
        changeRecurring(event) {
            if (event.target.checked == true) {
                this.$emit('recurringChange', true);
                this.isDraftMode = true;
                if (this.isDraftMode == true) {
                    this.getLatestInvoiceNumber();
                }
                this.FormSchema[0].Data[0].value = '';
                this.FormSchema[0].Data[1].value = this.isDraftMode ? '' : 'Draft Mode';
                const label = this.isDraftMode ? "Invoice Number" : "Bill Status";
                this.FormSchema[0].Data[1].label = label;
                this.filteredOptions = [];
                this.selectedProjects = []
                this.isHideMessage = true;
            } else {
                this.FormSchema[0].Data[0].value = '';
                this.filteredOptions = [];
                this.selectedProjects = []
                this.$emit('recurringChange', false);
                this.isHideMessage = false;
            }
        },

        async getClients() {
            debugger;
            const vm = this;
            vm.isLoading = true;
            try {
                const url = 'type=CLIENT&projectOrClientId';
                const response = await DataService.GetClientsAndProjects(url);
                if (response?.data?.result) {
                    const responseData = JSON.parse(response.data.result).data;
                    if (Array.isArray(responseData) && responseData.length > 0) {
                        responseData.forEach(item => {
                            if (item.Name && item.Id && typeof item.Id === 'string' && item.Id.trim() !== '') {
                                try {

                                    if (this.Formtype == 'Create') {

                                        vm.FormSchema[0].Data[0].config.options.push({
                                            name: item.Name,
                                            value: item.Id
                                        });
                                    }
                                    else {

                                        vm.FormSchema[0].Data[0].config.options.push({
                                            name: item.Name,
                                            value: item.Id.split('^')[0]
                                        });
                                    }

                                    vm.$refs.invoiceFields.UpdateKeyValue();
                                } catch (error) {
                                    console.error('Error processing item:', item, error);
                                }
                            }
                        });
                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching clients:', error);
            } finally {
                vm.isLoading = false;
            }
        },

        async projectListByClientId(event, item, editid) {
            debugger;
            const vm = this;
            vm.isLoading = true;

            // vm.FormSchema[0].Data[2].config.options = [];
            vm.filteredOptions = [];
            vm.selectedProjects = []
            vm.Bill_To = '';

            try {

                let id, email;
                if (this.Formtype === 'Create') {
                    [id, email] = item.value.split('^');
                    vm.Bill_To = id;
                    vm.InvoiceComponentFieldsValues();
                    vm.$emit('emailDetail', email);

                } else {

                    id = editid;
                }

                const url = `type=PROJECT&projectOrClientId=${id}`;

                const response = await DataService.GetClientsAndProjects(url);
                if (response?.data?.result) {
                    const responseData = JSON.parse(response.data.result).data;
                    if (Array.isArray(responseData) && responseData.length > 0) {
                        responseData.forEach(item => {
                            if (item.Name && item.Id && typeof item.Id === 'string' && item.Id.trim() !== '') {
                                try {
                                    // vm.FormSchema[0].Data[2].config.options.push({
                                    //     name: item.Name,
                                    //     value: item.Id
                                    // });
                                    // const idValue = item.Id.split('^')[0];

                                    if (this.Formtype == 'Add') {

                                        let obj = {

                                            label: item.Name,
                                            value: item.Id
                                        }
                                        vm.filteredOptions.push(obj);
                                    }
                                    else {

                                        let obj = {

                                            label: item.Name,
                                            value: item.Id.split('^')[0]
                                        }
                                        vm.filteredOptions.push(obj);
                                    }
                                    // vm.$refs.invoiceFields.UpdateKeyValue();
                                } catch (error) {
                                    console.error('Error processing item:', item, error);
                                }
                            }
                        });
                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching clients:', error);
            } finally {
                vm.isLoading = false;
            }
        },
        async getTemplates() {
            const vm = this;
            vm.isLoading = true;
            try {
                const response = await DataService.GetTemplates();
                if (response?.data?.result) {
                    const responseData = JSON.parse(response.data.result).data;
                    if (Array.isArray(responseData) && responseData.length > 0) {

                        responseData.map(item => {
                            vm.templateList.push({
                                name: item.TEMPLATE_NAME,
                                value: item.TEMPLATE_ID
                            });
                        });
                        vm.$refs.invoiceFields.UpdateKeyValue();
                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching clients:', error);
            } finally {
                vm.isLoading = false;
            }
        },
        handleTemplateChange(templateId) {
            this.TemplateId = '';
            this.TemplateId = templateId;
            this.InvoiceComponentFieldsValues();
        },
        async previewTemplate() {
            const vm = this;
            vm.isLoading = true;
            if (this.TemplateId == '')
                return this.ShowAlert(this.$t("Please select a template"), "warning", this.$t('Alert'));

            try {
                let url = `templateId=${this.TemplateId}`;
                const response = await DataService.GetPreviewTemplate(url);
                if (response?.data?.result) {
                    const responseData = JSON.parse(response.data.result).data;
                    if (Array.isArray(responseData) && responseData.length > 0) {
                        vm.previewTemplateContent = responseData[0]?.template_content;
                        vm.isViewTemplate = true;
                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching templates:', error);
            } finally {
                vm.isLoading = false;
            }
        },
        async getLatestInvoiceNumber() {
            const vm = this;
            vm.isLoading = true;
            vm.FormSchema[0].Data[1].value = '';
            try {
                const response = await DataService.GetLatestInvoiceNumber();
                if (response?.data?.result) {
                    const InvoiceNumber = response.data.result;
                    vm.FormSchema[0].Data[1].value = InvoiceNumber;
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching InvoiceNumber:', error);
            } finally {
                vm.isLoading = false;
            }
        },

        async getInitialData() {
            const vm = this;
            vm.isLoading = true;
            try {
                const response = await DataService.GetInitialData();
                if (response.data != null) {
                    console.log("InitialData", response.data);
                    let BillSetting = response.data.billSetting;
                    let CompanyDetail = response.data.companyDetail;
                    let SharedCompanyList = response.data.sharedCompanyList;
                    console.log("BillSetting", BillSetting)
                    console.log("CompanyDetail", CompanyDetail)
                    console.log("SharedCompanyList", SharedCompanyList)
                    vm.IsSharedCompany = false;
                    if (SharedCompanyList.length > 0) {
                        vm.IsSharedCompany = true;
                    } else {
                        vm.IsSharedCompany = false;
                    }

                } else {
                    //  console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Initial Data:', error);
            } finally {
                vm.isLoading = false;
            }
        },
        InvoiceComponentFieldsValues() {
            let vm = this;
            let Bill_Id = vm.Formtype == 'Create' ? 0 : vm.InvoiceFieldsDetails.bill_id;
            let Bill_To = vm.Bill_To;
            let Invoice_Number = vm.$refs.invoiceFields.getPropertyValue("Invoice_Number", "value");
            let Project = vm.selectedProjects.map(option => option.value.split('^')[0]).join(',');
            let Date_From = vm.$refs.invoiceFields.getPropertyValue("Date_From", "value");
            let Date_To = vm.$refs.invoiceFields.getPropertyValue("Date_To", "value");
            let Issue_Date = vm.$refs.invoiceFields.getPropertyValue("Issue_Date", "value");
            let Due_Date = vm.$refs.invoiceFields.getPropertyValue("Due_Date", "value");
            let Template = vm.$refs.invoiceFields.getPropertyValue("Template", "value");
            let Due_Date_Comment = vm.$refs.invoiceFields.getPropertyValue("Due_Date_Comment", "value");
            let Terms = vm.$refs.invoiceFields.getPropertyValue("Terms", "value");
            let currencyId = this.currencyId;
            let PayTo = 1;
            let IsSharedCompany = this.IsSharedCompany;
            let invoiceDetails = {
                Bill_Id,
                Bill_To,
                Invoice_Number,
                Project,
                Date_From,
                Date_To,
                Issue_Date,
                Due_Date,
                Template,
                Due_Date_Comment,
                Terms,
                currencyId,
                PayTo,
                IsSharedCompany
            };
            this.$emit("InvoiceFieldsChange", invoiceDetails);
        }
    },

}
</script>
<style></style>