<template>
    <div v-bind:class="{
        'tt_addcategory_ddl': true,
        'multiselect-dropdown': true,
        'disabled': Formtype != 'Create'
    }" ref="dropdown" @click="toggleDropdown">
        <span v-if="selectedOptions.length === 0" class="placeholder">{{ placeholder }}</span>
        <span v-else>
            <span v-for="(option, index) in visibleSelectedOptions" :key="option.value" class="optext">
                {{ option.label }}
                <span class="optdel" @click.stop="removeOption(index)"></span>
            </span>
            <span v-if="hiddenOptionsCount > 0" class="more-indicator text-primary" @click="toggleDropdown">
                +{{ hiddenOptionsCount }} more
            </span>


            <span class="ttddl_clearall bg-danger" @click="clearAll"><i class="fa fa-refresh"></i></span>

        </span>
        <div class="multiselect-dropdown-list-wrapper">

            <input v-if="isVisibleSearchInput" type="text" class="multiselect-dropdown-search " v-model="searchQuery"
                :placeholder="searchPlaceholder" />

            <div v-if="showDropdown" class="multiselect-dropdown-list">

                <div v-if="selectAllEnabled && filteredOptions.length > 1" class="multiselect-dropdown-all-selector"
                    @click="toggleSelectAll">

                    <div class="form-check form-check-inline p-0">
                        <div class="custom-control custom-checkbox">
                            <input type="checkbox" class="chkItems custom-control-input" :checked="isAllSelected"><label
                                for="allCheck" class="custom-control-label universal-custom-control-label pt-1"></label>
                        </div>
                    </div>


                    <label>{{ allText }}</label>

                </div>



                <div v-if="filteredOptions.length === 0" class="no-records-found">
                    No records found
                </div>

                <div v-else v-for="option in filteredOptions" :key="option.value"
                    class="multiselect-dropdown-single-selector" :class="{ checked: isSelected(option) }"
                    @click="toggleOption(option)">


                    <div class="form-check form-check-inline p-0">
                        <div class="custom-control custom-checkbox">
                            <input type="checkbox" :checked="isSelected(option)" class="chkItems custom-control-input">
                            <label for="allCheck" class="custom-control-label universal-custom-control-label pt-1">
                            </label>
                        </div>
                    </div>

                    <label class="p-1">{{ option.label }}</label>

                </div>

            </div>

        </div>
    </div>
</template>

<script>
export default {
    name: 'MultiselectDropdown',
    props: {
        options: {
            type: Array,
            required: true
        },
        value: {
            type: Array,
            default: () => []
        },
        placeholder: {
            type: String,
            default: 'Select'
        },
        searchable: {
            type: Boolean,
            default: true
        },
        searchPlaceholder: {
            type: String,
            default: 'Search'
        },
        maxItems: {
            type: Number,
            default: 3
        },
        selectAllEnabled: {
            type: Boolean,
            default: true
        },
        allText: {
            type: String,
            default: 'Select All'
        },
        Formtype: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            showDropdown: false,
            searchQuery: '',
            selectedOptions: [...this.value],
            isVisibleSearchInput: false,
        };
    },
    computed: {

        visibleSelectedOptions() {

            return this.selectedOptions.slice(0, this.maxItems);
        },
        hiddenOptionsCount() {

            return this.selectedOptions.length - this.maxItems;
        },
        filteredOptions() {

            const query = this.searchQuery.trim().toLowerCase();
            return this.options.filter(option => option.label.toLowerCase().includes(query));
        },
        isAllSelected() {
            return this.filteredOptions.length > 0 &&
                this.filteredOptions.every(option => this.selectedOptions.includes(option));

        }
    },

    methods: {

        toggleDropdown() {
            if (this.isVisibleSearchInput == false) {
                this.isVisibleSearchInput = true;
                this.showDropdown = true;
            } else {
                if (this.showDropdown == true) {
                    this.showDropdown = true;
                }
                else {
                    this.showDropdown = false
                }
            }
            this.searchQuery = '';
        },
        toggleOption(option) {
            const index = this.selectedOptions.findIndex(selected => selected.value === option.value);
            if (index > -1) {
                this.selectedOptions.splice(index, 1);
            } else {
                this.showDropdown = true;
                this.searchQuery = ''
                this.isVisibleSearchInput = true
                this.selectedOptions.push(option);
            }
            this.$emit('input', this.selectedOptions, option);
        },
        removeOption(index) {
            this.selectedOptions.splice(index, 1);
            this.$emit('input', this.selectedOptions);
        },

        isSelected(option) {
            return this.selectedOptions.some(selected => selected.value === option.value);
        },

        toggleSelectAll() {
            let optionsToSelect;
            if (this.isAllSelected) {
                this.selectedOptions = [];

            } else {
                optionsToSelect = this.filteredOptions.filter(option => !this.isSelected(option));
                // if (this.maxItems > 0) {
                //     this.selectedOptions = [...this.selectedOptions, ...optionsToSelect.slice(0, this.maxItems - this.selectedOptions.length)];
                // } else {
                this.selectedOptions = [...this.selectedOptions, ...optionsToSelect];
                // }
            }
            this.$emit('input', this.selectedOptions);
        },


        closedropdown() {
            this.showDropdown = false;
            this.isVisibleSearchInput = false;
        },

        clearAll() {
            this.selectedOptions = [];
            this.$emit('input', this.selectedOptions);
        },

        handleClickOutside(event) {
            const dropdown = this.$refs.dropdown;
            if (dropdown && !dropdown.contains(event.target)) {
                this.closedropdown();
            }
        }


    },
    watch: {
        value(newVal) {
            this.selectedOptions = [...newVal];
        }
    },

    mounted() {

        document.addEventListener('click', this.handleClickOutside);
    },
    beforeDestroy() {
        document.removeEventListener('click', this.handleClickOutside);
    }
};
</script>

<style>
.loader {
    border: 4px solid #f3f3f3;
    /* Light grey */
    border-top: 4px solid #3498db;
    /* Blue */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
</style>
