<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex align-items-center">
                            <h4 class="modal-title">{{ $t('TemplateReview') }}</h4>
                            <button type="button" class="close" @click="closeTemplateReview" aria-label="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <div class="table-responsive" style="background:#fff;">
                                <div v-if="PreviewTemplate && PreviewTemplate.trim() !== ''" v-html="PreviewTemplate">
                                </div>

                                <div v-else class="text-center text-danger no-record">
                                    {{ noRecordMessage }}
                                </div>
                            </div>
                        </div>

                        <div class="modal-footer">
                            <button type="button" class="btn btn-danger" @click="closeTemplateReview">
                                <i class="fa fa-close pr-2"></i>Close
                            </button>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    props: {
        PreviewTemplate: {
            type: String,
            required: false,
            default: ''
        }
    },
    data() {
        return {
            isLoading: false,
            templateContent: '',
            noRecordMessage: "No record found"
        };
    },
    watch: {
        PreviewTemplate(newValue) {
            this.templateContent = newValue;
        }
    },
    created() {
        console.log('Templates loaded successfully:', this.PreviewTemplate);
    },
    methods: {
        closeTemplateReview() {
            this.$parent.isViewTemplate = false;
        }
    }
};
</script>

<style scoped>
table tr td {
    border-bottom: none !important;
}
</style>