<template>
    <div>
        <div class="row">
            <div class="col-lg-12">
                <div class="heading-border mb-3 mt-3">
                    <h5 class="h5">
                        <span class="d-md-inline-block">
                            Invoice Items
                        </span>
                    </h5>
                </div>
            </div>
        </div>


        <div class="row form_row" id="inventor">

            <div class="col-lg-12">
                <div class="table-responsive mb-3">
                    <Form ref="invoiceItemsFields" v-slot="{ valid, errors }">
                        <table id="tblInvoiceItem" class="invoiceitem table table-bordered dt-responsive nowrap mb-0">
                            <thead class="thead-bg">
                                <tr>
                                    <th width="18%" class="td2 tdcm">{{ $t('ItemName') }}</th>
                                    <th width="40%" class="td3">{{ $t('DESCRIPTION') }}</th>
                                    <th width="20%" class="td4 tdcm" align="center">{{ $t('RateXUnits') }}</th>
                                    <th width="10%" class="td5"> {{ $t('Price') }}<label for="lblPriceCurrency">{{
                                        (GetUserInfo.Currency) }}</label></th>
                                    <th width="10%" class="td1 tdcm text-center">{{ $t('Action') }}</th>
                                    <th width="6%">&nbsp;</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in updateInvoiceItems" :key="index">

                                    <td>

                                        <Field name="Item Name" :rules="{ 'required': true }" v-slot="{ errors }">
                                            <div class="input-group">
                                                <input type="text" id="" v-model="item.name"
                                                    placeholder="Enter Item name"
                                                    v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                            </div>
                                            <span v-if="errors.length" class="invalid-feedback d-block">{{ errors[0]
                                                }}</span>
                                        </Field>

                                    </td>

                                    <td>

                                        <Field name="Description" :rules="{ 'required': false }" v-slot="{ errors }">
                                            <div class="input-group">
                                                <input type="text" id="" v-model="item.description"
                                                    placeholder="Enter Item Description"
                                                    v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                            </div>
                                            <span v-if="errors.length" class="invalid-feedback d-block">{{ errors[0]
                                                }}</span>
                                        </Field>

                                    </td>

                                    <td class="td4 tdcm">
                                        <div class="d-flex">
                                            <Field name="Rate" :rules="{ 'required': true }" v-slot="{ errors }">
                                                <div class="input-group">
                                                    <input type="number" id="Rate" v-model="item.rate"
                                                        placeholder="Enter a rate" @keydown="validateKey"
                                                        @keyup="calculatePrice(item, 'rate')"
                                                        v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                                </div>
                                                <span v-if="errors.length" class="invalid-feedback d-block">{{ errors[0]
                                                    }}</span>

                                            </Field>

                                            <div class="input-group-apend">
                                                <span class="input-group-text border-0 bg-transparent"> x</span>
                                            </div>

                                            <Field name="Units" :rules="{ 'required': true }" v-slot="{ errors }">
                                                <div class="input-group">
                                                    <input type="number" id="Units" v-model="item.units"
                                                        @keydown="validateKey" @keyup="calculatePrice(item, 'unit')"
                                                        placeholder="Enter a unit"
                                                        v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                                </div>
                                                <span v-if="errors.length" class="invalid-feedback d-block">{{ errors[0]
                                                    }}</span>
                                            </Field>
                                        </div>
                                    </td>


                                    <td class="td5 tdcm">
                                        <Field name="Price" :rules="{ 'required': false }" v-slot="{ errors }">
                                            <div class="input-group">
                                                <input type="number" id="Price" v-model="item.price" :disabled="true"
                                                    v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                            </div>
                                            <span v-if="errors.length" class="invalid-feedback d-block">{{ errors[0]
                                                }}</span>
                                        </Field>
                                    </td>


                                    <td class="single-action text-center">
                                        <a @click="removeItem(index)" v-if="updateInvoiceItems.length >= 1"
                                            :class="{ 'text-danger': updateInvoiceItems.length > 1, 'disabled': updateInvoiceItems.length <= 1 }">
                                            <em class="fa fa-trash"></em>
                                        </a>
                                    </td>

                                </tr>
                            </tbody>
                            <tfoot>
                                <tr class="ui-state-default">
                                    <td></td>
                                    <td></td>
                                    <td class="td4 arialb000000 text-right align-middle">
                                        Sub Total <label for="lblSubTotalCurrency">{{ (GetUserInfo.Currency) }}</label>:
                                    </td>
                                    <td class="td5 tdcm">
                                        <input v-model="subTotal" class="form-control pay_field2 number bg-white"
                                            type="text" readonly />
                                    </td>
                                    <td class="td1 tdcm"></td>
                                </tr>
                            </tfoot>
                        </table>
                    </Form>
                </div>
            </div>
        </div>

        <div class="row">


            <div class="col-lg-6">
                <div class="form-group">
                    <div id="CommentDiv" class="form_row">
                        <textarea class="form-control addinvoice" v-model="billDescription" maxlength="500"
                            placeholder="Enter a Comment" rows="6" id="BillDescription" name="BillDescription"></textarea>
                    </div>
                </div>
            </div>


            <div class="col-lg-6">
                <div class="form-group text-right">

                    <div class="py-2 pl-3 bg-grey">

                        <a id="addnewLine" class="btn btn-info mr-1" href="javascript:;" title=""
                            data-toggle-tooltip="tooltip" v-on:click="addInvoiceItems(valid, errors)"
                            data-original-title="Add New Row">{{ $t('AddNewRow') }}</a>
                        <!-- <a id="addFromTimeSheet" class="btn btn-primary mr-1" href="javascript:;">Add from Time /
                            Expense
                            sheet.</a>
                        <a id="addFromTimeTrack" class="btn btn-info mr-1" href="javascript:;">Get Time Track</a>
                        <a id="addFromWorkOrder" v-on:click="addWorkOrder()" class="btn btn-warning mr-1"
                            href="javascript:;">Work
                            Order</a>
                        <a id="deleteExpense" class="del_cross_exp btn btn-danger mr-1" href="javascript:;">Delete all
                            Expenses</a>
                        <a id="deleteTimeEntry" class="del_cross_task btn btn-danger" href="javascript:;">Delete all
                            Tasks</a> -->

                    </div>

                </div>

                <Discounts :InvoiceItemsTotalPrice="subTotal" @updateDiscountTotal="updateDiscountTotal"
                    @DiscountItemsDetails="handleDiscountItemsDetails" :discountDetails="discountDetails"
                    :InvoiceFieldsDetails="InvoiceFieldsDetails"></Discounts>

                <Taxes :taxDetails="taxDetails" :discountTotalAmount="subDiscountTotal"
                    @TaxItemsDetails="handleTaxItemsDetails" :InvoiceFieldsDetails="InvoiceFieldsDetails"></Taxes>

                <WorkOrder v-if="isWorkOrder"></WorkOrder>

            </div>
        </div>


    </div>
</template>

<script>

import Discounts from './Discounts.vue';
import Taxes from './Taxes.vue';
import WorkOrder from './WorkOrder.vue';

export default {

    components: {
        Discounts,
        Taxes,
        WorkOrder
    },
    computed: {
        subTotal() {
            return this.invoiceItems.reduce((total, item) => total + (parseFloat(item.price) || 0), 0);
        },
        updateInvoiceItems() {
            this.InvoiceItemsDetails();
            return this.invoiceItems;
        }
    },

    props: {
        invoiceItemDetails: {
            type: Array,
        },
        InvoiceFieldsDetails: {
            type: Object,

        },
        discountDetails: {
            type: Array
        },
        taxDetails: {
            type: Array
        }
    },
    watch: {
        invoiceItemDetails(newValue, oldValue) {
            this.invoiceItems = newValue;
        },
        InvoiceFieldsDetails(newValue, oldValue) {
            this.billDescription = newValue.bill_description;
        }
    },
    data() {
        return {
            invoiceItems: [
                {
                    name: '',
                    description: '',
                    rate: '',
                    units: '',
                    price: 0
                }
            ],
            billDescription: '',
            subDiscountTotal: 0,
            isWorkOrder: false
        }
    },

    created() {


    },
    methods: {

        updateDiscountTotal(discountAmount) {
            this.subDiscountTotal = parseInt(discountAmount);
        },
        addInvoiceItems(valid, errors) {
            let validationCheck = false;
            this.invoiceItems.forEach((item) => {
                if (item.name == '' && item.rate == '' && item.units == '') {
                    validationCheck = true;
                    this.$refs.invoiceItemsFields.validate();
                }
            });
            if (validationCheck == false) {
                this.$refs.invoiceItemsFields.validate().then(result => {
                    if (result) {
                        this.invoiceItems.push({
                            name: '',
                            description: '',
                            rate: '',
                            units: '',
                            price: 0
                        });
                    } else {
                        this.ShowAlert(this.$t("Please correct the highlighted errors"), "failure", this.$t('Alert'));
                    }
                }).catch(error => {
                    console.error("Validation error: ", error);
                });
            } else {
                this.ShowAlert(this.$t("Please correct the highlighted errors"), "failure", this.$t('Alert'));
            }
        },


        async removeItem(index) {
            try {

                const result = await new Promise((resolve) => {
                    this.confirmR(this.$t('Are you sure you want to delete these record(s)?'), true, false, resolve);
                });

                if (result == undefined) {
                    this.invoiceItems.splice(index, 1);
                    this.$refs.invoiceItemsFields.$reset()
                    this.ShowAlert(
                        this.$t("Item has been deleted Successfully "),
                        "success",
                        this.$t("Alert")
                    );
                } else {
                    //
                }
            } catch (error) {
                // 
                console.error("Error during confirmation:", error);
            }
        },

        calculatePrice(item, type) {
            if (type === 'rate' || type === 'unit') {
                item.price = (parseFloat(item.rate) || 0) * (parseFloat(item.units) || 0);
            }
        },
        validateKey(event) {
            const disallowedKeys = ['+', '-', '=', 'e'];
            const allowedKeys = [
                'Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab', 'Enter', '.'
            ];
            if (disallowedKeys.includes(event.key)) {
                event.preventDefault();
            }
            if ((event.key >= '0' && event.key <= '9') || allowedKeys.includes(event.key)) {
                return;
            }
            event.preventDefault();
        },

        InvoiceItemsDetails() {
            let vm = this;
            let invoiceItems = vm.invoiceItems;
            let subTotal = vm.subTotal;
            let billDescription = this.billDescription;
            let invoiceItemsDetails = {
                invoiceItems,
                subTotal,
                billDescription
            };
            this.$emit("InvoiceItemsDetails", invoiceItemsDetails);
        },

        handleDiscountItemsDetails(discountItemsDetails) {
            this.$emit("DiscountItemsDetails", discountItemsDetails);
        },
        handleTaxItemsDetails(taxItemsDetails) {
            this.$emit("TaxItemsDetails", taxItemsDetails);
        },
        addWorkOrder() {
            debugger;
            this.isWorkOrder = true;

        }


    },
}
</script>
<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>