<template>
    <div class="listing">
        <div class="table-responsive position-relative overflow-visible">
            <Form ref="discountsItemsFields" v-slot="{ valid, errors }">
                <table class="table table-bordered" id="billAdjustment">
                    <thead class="thead-bg">
                        <tr role="row">
                            <th colspan="3">

                                <label class="pr-3">{{ $t('Discounts') }}:</label>

                                <div class="custom-control custom-radio custom-control-inline">
                                    <input type="radio" data-resource="Is Sequential" v-model="discountType"
                                        value="Sequential" @change="handleDiscountTypeChange($event, 'Sequential')"
                                        id="rdo_1" class="custom-control-input" />
                                    <label class="custom-control-label" for="rdo_1">{{ $t('IsSequential') }}</label>
                                </div>

                                <div class="custom-control custom-radio custom-control-inline">
                                    <input type="radio" data-resource="Is Stacked" v-model="discountType" value="Stack"
                                        @change="handleDiscountTypeChange($event, 'Stack')" id="rdo_2"
                                        class="custom-control-input" />
                                    <label class="custom-control-label" for="rdo_2">{{ $t('IsStacked') }}</label>
                                </div>

                            </th>

                            <th class="td3 tdrrm_nd_bold" style="width: 120px;">
                                <a href="javascript:;" id="ancDiscount" @click="addDiscountRow(valid, errors)" title=""
                                    class="btn btn-primary float-right adinvc" data-toggle-tooltip="tooltip"
                                    data-original-title="Add More">
                                    <i class="fa fa-plus pr-2" aria-hidden="true"></i>{{ $t('Add') }}
                                </a>
                            </th>

                        </tr>
                    </thead>

                    <tbody>

                        <tr v-for="(discount, index) in discountAddUpadate" :key="index" class="ui-state-default">

                            <td width="48%" class="">

                                <Field name="Discount name" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <div class="input-group">
                                        <input type="text" id="label" v-model="discount.label" maxlength="50"
                                            placeholder="Enter discount name"
                                            v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                    </div>
                                    <!-- <span v-if="errors.length" class="text-danger">{{ errors[0] }}</span> -->
                                </Field>

                            </td>

                            <td width="20%" class="">

                                <Field name="Percentage" :rules="{ 'required': true }" v-slot="{ errors }">

                                    <div class="input-group">
                                        <input type="decimal" id="label" v-model="discount.percentage"
                                            @keydown="validateKey($event)" @input="validatePercentage(index)"
                                            placeholder="Enter percentage" maxlength="5"
                                            v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] }" />
                                        <div class="input-group-append discpercentage">
                                            <span class="input-group-text">%</span>
                                        </div>
                                    </div>
                                    <!-- <span v-if="errors.length" class="text-danger">{{ errors[0] }}</span> -->

                                </Field>

                            </td>

                            <td width="20%" class="">

                                <Field name="Total" :rules="{ 'required': false }" v-slot="{ errors }">

                                    <div class="input-group">
                                        <input type="decimal" v-model="discount.total" readonly
                                            class="price_paddright3 form-control invoicebox2_nd" autocomplete="off" />
                                    </div>

                                </Field>

                            </td>

                            <td width="8%" class="td4 single-action text-center">
                                <a href="javascript:;" @click="removeDiscountRow(index)" class="addDiscount text-danger"
                                    title="" data-toggle-tooltip="tooltip" data-original-title="Delete">
                                    <em class="fa fa-trash"></em>
                                </a>
                            </td>

                        </tr>
                    </tbody>
                    <tfoot>
                        <tr class="ui-state-default">
                            <td></td>

                            <td class="td4 arialb000000 text-right align-middle">
                              {{ $t('TotalDiscount') }}<label for="lblSubTotalCurrency">{{ (GetUserInfo.Currency) }}</label>:
                            </td>
                            <td class="td5 tdcm">
                                <input v-model="discountTotal" class="form-control pay_field2 number bg-white"
                                    type="text" readonly />
                            </td>
                            <td class="td1 tdcm"></td>
                        </tr>
                        <tr class="ui-state-default">
                            <td></td>

                            <td class="td4 arialb000000 text-right align-middle">
                                Sub total after discount
                                <label for="lblSubTotalCurrency"> {{ (GetUserInfo.Currency) }}</label>:
                            </td>
                            <td class="td5 tdcm">
                                <input v-model="discountTotalAmount" class="form-control pay_field2 number bg-white"
                                    type="text" readonly />
                            </td>
                            <td class="td1 tdcm"></td>
                        </tr>
                    </tfoot>
                </table>
            </Form>
        </div>
    </div>
</template>

<script>
export default {
    computed: {
        discountAddUpadate() {
            this.DiscountItemsDetails();
            return this.discounts;
        },
        discountTotal() {
            return this.totalDiscount.toFixed(2);
        },
        discountTotalAmount() {
            const discountAmount = this.totalAmount();
            this.$emit('updateDiscountTotal', discountAmount);
            return discountAmount;
        }

    },
    props: {
        InvoiceItemsTotalPrice: {
            type: Number,
        },
        discountDetails: {
            type: Array
        },
        InvoiceFieldsDetails: {
            type: Object,

        }

    },
    watch: {
        InvoiceItemsTotalPrice(newValue, oldValue) {
            this.originalPrice = newValue;
            this.InvoiceTotalPrice = newValue;
            this.calInvoiceTotalPrice = newValue;
            this.handleDiscountType();
            this.applyDiscounts();
        },

        discountDetails(newValue, oldValue) {
            this.discounts = newValue
        },
        InvoiceFieldsDetails(newValue, oldValue) {
            if (newValue.DiscountType == 1) {
                this.handleDiscountTypeChange('', 'Stack')
            }
            else {
                this.handleDiscountTypeChange('', 'Sequential');
            }

        }
    },

    data() {
        return {
            discountType: 'Sequential',
            discounts: [
                {
                    label: '',
                    percentage: '',
                    total: '',
                },
            ],
            originalPrice: 0,
            InvoiceTotalPrice: 0,
            totalDiscount: 0,
            calInvoiceTotalPrice: 0
        };
    },
    created() {
        //

    },
    methods: {
        totalAmount() {
            if (this.discountType === 'Sequential') {
                return this.InvoiceTotalPrice.toFixed(2);
            } else if (this.discountType === 'Stack') {
                let totalStackedAmount = this.originalPrice - this.totalDiscount;
                return totalStackedAmount.toFixed(2);
            }
        },
        handleDiscountType() {
            if (this.discountType === 'Sequential') {
                this.totalDiscount = 0
                this.InvoiceTotalPrice = this.calInvoiceTotalPrice;
            } else if (this.discountType === 'Stack') {
                this.totalDiscount = 0
                this.InvoiceTotalPrice = 0
            }
        },
        applyDiscounts() {
            this.discounts.forEach((discount, index) => {
                this.validatePercentage(index);
            });
        },
        validateKey(event) {
            const disallowedKeys = ['+', '-', '=', 'e'];
            const allowedKeys = [
                'Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab', 'Enter', '.'
            ];
            if (disallowedKeys.includes(event.key)) {
                event.preventDefault();
            }
            if ((event.key >= '0' && event.key <= '9') || allowedKeys.includes(event.key)) {
                return;
            }
            event.preventDefault();
        },
        addDiscountRow(valid, errors) {
            let validationCheck = false;
            this.discounts.forEach((item) => {
                if (item.label == '' && item.percentage == '') {
                    validationCheck = true;
                    this.$refs.discountsItemsFields.validate();
                }
            });
            if (validationCheck == false) {

                this.$refs.discountsItemsFields.validate().then(result => {
                    if (result) {

                        this.discounts.push({
                            label: '',
                            percentage: '',
                            total: '',
                        });

                    } else {
                        this.ShowAlert(this.$t("Please correct the highlighted errors"), "failure", this.$t('Alert'));
                    }
                }).catch(error => {
                    console.error("Validation error: ", error);
                });
            } else {
                this.ShowAlert(this.$t("Please correct the highlighted errors"), "failure", this.$t('Alert'));
            }
        },
        handleDiscountTypeChange($event, type) {
            this.discountType = type;
            this.handleDiscountType();
            this.applyDiscounts();
        },
        async removeDiscountRow(index) {
            try {

                const result = await new Promise((resolve) => {
                    this.confirmR(this.$t('Are you sure you want to delete these record(s)?'), true, false, resolve);
                });

                if (result == undefined) {
                    this.discounts.splice(index, 1);

                    this.handleDiscountType();
                    this.applyDiscounts();

                    this.ShowAlert(
                        this.$t("Item has been deleted Successfully "),
                        "success",
                        this.$t("Alert")
                    );

                } else {
                    //
                }
            } catch (error) {
                console.error("Error during confirmation:", error);
            }
        },
        validatePercentage(index) {
            const discount = this.discounts[index];
            let percentage = parseFloat(discount.percentage);
            if (isNaN(percentage)) {
                return percentage = 0;
            }
            if (this.discountType === 'Sequential') {
                discount.total = this.calculateSequentialDiscounts(index, percentage);
            } else if (this.discountType === 'Stack') {
                discount.total = this.calculateStackedDiscounts(index, percentage);
            }
        },
        calculateSequentialDiscounts(index) {
            let totalAfterDiscount = this.InvoiceTotalPrice;
            let totalDiscount = this.totalDiscount;
            let currentDiscountAtIndex = totalAfterDiscount * (this.discounts[index].percentage / 100);
            for (let i = 0; i <= index; i++) {
                if (i == index) {
                    let currentDiscount = totalAfterDiscount * (this.discounts[i].percentage / 100);
                    totalDiscount += currentDiscount;
                    totalAfterDiscount -= currentDiscount;
                }
            }
            this.InvoiceTotalPrice = totalAfterDiscount;
            this.totalDiscount = totalDiscount;
            return currentDiscountAtIndex.toFixed(2);
        },
        calculateStackedDiscounts(index, percentage) {
            let currentDiscount = 0;
            let totalDiscount = 0;
            for (let i = 0; i <= index; i++) {
                totalDiscount += this.originalPrice * (this.discounts[i].percentage / 100);
            }
            this.totalDiscount = totalDiscount;
            currentDiscount = this.originalPrice * (this.discounts[index].percentage / 100);
            return currentDiscount;
        },

        DiscountItemsDetails() {
            let vm = this;
            let discounts = vm.discounts;
            let discountTotal = vm.discountTotal;
            let discountTotalAmount = vm.discountTotalAmount;
            let discountType = vm.discountType;
            let discountItemsDetails = {
                discounts,
                discountTotal,
                discountTotalAmount,
                discountType
            };
            this.$emit("DiscountItemsDetails", discountItemsDetails);
        }


    },

};
</script>

<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>