<template>
    <div>
        <section class="page-action">

            <loader :is-visible="isLoading"></loader>

            <Form class="row" ref="AddInvoice" v-slot="{ valid, errors }">

                <div class="container-fluid  ">
                    <div class="row">
                        <div class="col-md-12 padding-t_8">
                            <div class="theme-primary partition-full">
                                <span class="p-name text-white">{{ Formtype == 'Create' ? ' Add Invoice' : 'Edit Client Invoice'}}</span>
                                <span class="p-actions float-right">

                                    <router-link to="/ClientBill/Index"
                                        class="p-action-btn text-white d-none d-md-inline-block"
                                        data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                                            class="fa fa-angle-double-left" aria-hidden="true"></i><br> {{ $t('BacktoList') }}
                                    </router-link>

                                </span>
                            </div>
                        </div>
                    </div>

                    <div class="container-fluid p-0">
                        <div class="border p-3">

                            <!-- 
                            @if (Model.IsSharedCompany.GetValueOrDefault() == true)
                            {
                            <div class="col-lg-3">
                                <div class="form-group">
                                    <asp-label-mandatory asp-for="PayTo"></asp-label-mandatory>
                                    <div class="pl-0 border-0">
                                        @if (isvendor.Trim() == "yes")
                                        {
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" value="3" id="rdoVendorTo" class="rdoPayTo"
                                                checked="checked" />
                                            <label class="custom-control-label"
                                                for="customRadio">@Resources.Vendor</label>
                                        </div>
                                        }
                                        else
                                        {
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" name="IssueTo" value="1" id="PayTo" class="rdoPayTo"
                                                checked="checked" />
                                            @Resources.Client
                                        </div>
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" name="IssueTo" value="2" id="PayTo"
                                                class="rdoPayTo marginleft10" />
                                            @Resources.Company
                                        </div>
                                        }

                                    </div>
                                </div>
                            </div>
                            }
                            else
                            {
                            <div class="col-lg-3 hidden">
                                <div class="form-group">
                                    <div class="custom-control custom-radio custom-control-inline">
                                        <input type="radio" value="1" id="PayTo" class="rdoPayTo marginleft10"
                                            checked="checked" />
                                        <label class="custom-control-label" for="customRadio">@Resources.Company</label>
                                    </div>
                                </div>
                            </div>
                            } -->

                            <div class="row">
                                <InvoiceFields @emailDetail="selectedEmailDetail"
                                    @InvoiceFieldsChange="handleInvoiceFieldsChange" @recurringChange="handleRecurring"
                                    :InvoiceFieldsDetails="InvoiceFieldsDetails" :Formtype="Formtype">
                                </InvoiceFields>
                            </div>

                            <Reccuring v-if="Reccuring" :emailDetail="emailDetail"
                                @RecurringFieldsChange="handleRecurringFieldsChange"
                                :recurringItemsDetails="recurringItemsDetails" :Formtype="Formtype"></Reccuring>

                            <InvoiceItems @InvoiceItemsDetails="handleInvoiceItemsDetails"
                                @DiscountItemsDetails="handleDiscountItems" @TaxItemsDetails="handleTaxItems"
                                :invoiceItemDetails="invoiceItemDetails" :discountDetails="discountDetails"
                                :taxDetails="taxDetails" :InvoiceFieldsDetails="InvoiceFieldsDetails">
                            </InvoiceItems>

                            <div class="row flex-row-reverse mt-3">

                                <div class="col-lg-6 text-right">

                                    <a class="btn btn-success mr-2"
                                        data-tour="step: 1; title: Enter Invoice Detail; content: Enter invoice detail and then save."
                                        v-on:click="saveInvoiceDetails(valid, errors)" href="javascript:;" id="aSave"><i
                                            class="fa fa-save pr-2"></i>{{ Formtype=='Create' ? 'Save':'Update' }} </a>
                                    <router-link to="/ClientBill/Index" title="" class="btn btn-danger"
                                        data-toggle="tooltip" data-original-title="Cancel">
                                        <i class="fa fa-close pr-2"></i> {{ $t('Cancel') }}
                                    </router-link>

                                </div>

                                <div class="col-lg-6">
                                    <div class="py-2"><span class="mandatory">Fields marked with an asterisk (*) are
                                            mandatory.</span></div>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>
            </Form>

        </section>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
import InvoiceFields from './InvoiceFields.vue';
import Reccuring from './Reccuring.vue';
import InvoiceItems from './InvoiceItems.vue';


export default {

    components: {
        InvoiceFields, Reccuring, InvoiceItems,
    },

    computed: {
        Reccuring() {
            return this.isRecurring;
        }
    },

    data() {
        return {
            isRecurring: false,
            emailDetail: {},
            invoiceDetails: {},
            recurringDetails: {},
            invoiceItemsDetails: {},
            discountItemsDetails: {},
            taxItemsDetails: {},
            isLoading: false,
            InvoiceFieldsDetails: {},
            invoiceItemDetails: [],
            discountDetails: [],
            taxDetails: [],
            Formtype: '',
            recurringItemsDetails: {}
        }
    },
    async created() {
        debugger;
        let billId = this.$route.params.id;
        this.Formtype = this.$route.params.action;
        if (billId != '0') {
            await this.getEditInvoiceDetails(billId)
        }
    },
    methods: {

        selectedEmailDetail(email) {
            this.emailDetail = {}
            this.emailDetail = {
                id: 0,
                email: email,
                errorMessage: ''
            }
        },

        handleRecurring(isRecurring) {
            this.isRecurring = isRecurring;
        },

        // saveInvoiceDetails(valid, errors) {
        //     debugger;
        //     this.$refs.AddInvoice.validate().then(result => {
        //         debugger;
        //         if (result) {

        //             let obj = {

        //                 invoiceDetails: this.invoiceDetails,
        //                 recurringDetails: this.recurringDetails,
        //                 invoiceItemsDetails: this.invoiceItemsDetails,
        //                 discountItemsDetails: this.discountItemsDetails,
        //                 taxItemsDetails: this.taxItemsDetails
        //             }
        //             try {

        //                 const postString = `postString=${JSON.stringify(obj)}&formType=Add`;
        //                 const response = await DataService.SaveAndUpdateManualInvoice(postString);
        //                 if (response.data.status === 'Success') {
        //                     console.log('Invoice saved successfully!');
        //                 } else {
        //                     console.log('Failed to save invoice:', response.data.message);
        //                 }
        //             } catch (error) {
        //                 console.log("Error message:", error);
        //             }

        //             // alert(JSON.stringify(obj));
        //             // console.log("Invoice", JSON.stringify(obj));
        //             // console.log("Invoice details saved!", obj);


        //         } else {
        //             this.ShowAlert(this.$t("Please correct the highlighted errors"), "failure", this.$t('Alert'));

        //         }
        //     });
        // },
        async saveInvoiceDetails(valid, errors) {
            try {
                const result = await this.$refs.AddInvoice.validate();

                if (result) {
                    this.isLoading = true;
                    let obj = {
                        invoiceDetails: this.invoiceDetails,
                        recurringDetails: this.recurringDetails,
                        invoiceItemsDetails: this.invoiceItemsDetails,
                        discountItemsDetails: this.discountItemsDetails,
                        taxItemsDetails: this.taxItemsDetails
                    };
                      console.log("Invoice", JSON.stringify(obj));
                    const postString = `postString=${JSON.stringify(obj)}&formType=${this.Formtype == 'Create' ? 'Save' : 'Update'}`;
                    const response = await DataService.SaveAndUpdateManualInvoice(postString);
                    if (response.data.result.StatusCode == '3') {
                        this.isLoading = false;
                        this.ShowAlert(this.$t("Duplicate invoice number"), "Error", this.$t('Alert'));

                        console.log('Invoice save failed due to duplicate number!');
                    } else if (response.data.result.StatusCode == '1') {
                        this.isLoading = false;
                        this.ShowAlert(this.$t("Invoice saved successfully"), "Success", this.$t('Alert'));
                        // setTimeout(() => {
                            this.$router.push('/ClientBill/Index')
                        // }, 3000);
                        console.log('Invoice saved successfully!');
                    } else if (response.data.result.StatusCode == '2') {
                        this.isLoading = false;
                        this.ShowAlert(this.$t("Invoice updated successfully"), "Success", this.$t('Alert'));
                        // setTimeout(() => {
                            this.$router.push('/ClientBill/Index')
                        // }, 3000);

                        console.log('Invoice updated successfully!');
                    }
                    else if (response.data.result.StatusCode == '-1') {
                        this.isLoading = false;
                        this.ShowAlert(this.$t(response.data.result.StatusMessage), 'failure', true, this.$t('Alert'));

                    }

                } else {
                    this.ShowAlert(this.$t("Please correct the highlighted errors"), "failure", this.$t('Alert'));
                }
            } catch (error) {
                console.log("Error message:", error);
            }
        },

        handleInvoiceFieldsChange(invoiceDetails) {
            this.invoiceDetails = {};
            invoiceDetails.isRecurring = this.Reccuring;
            this.invoiceDetails = { ...invoiceDetails };
            console.log("Received invoice fields details:", this.invoiceDetails);
        },

        handleRecurringFieldsChange(recurringDetails) {
            this.recurringDetails = {};
            this.recurringDetails = recurringDetails;
            console.log("Received recurring fields details:", recurringDetails);
        },
        handleInvoiceItemsDetails(invoiceItemsDetails) {
            this.invoiceItemsDetails = {};
            this.invoiceItemsDetails = invoiceItemsDetails;
            console.log("Received invoice items details:", invoiceItemsDetails);
        },
        handleDiscountItems(discountItemsDetails) {
            this.discountItemsDetails = {};
            this.discountItemsDetails = discountItemsDetails;
            console.log('Received discount items:', discountItemsDetails);
        },
        handleTaxItems(taxItemsDetails) {
            this.taxItemsDetails = {};
            this.taxItemsDetails = taxItemsDetails;
            console.log('Received tax items:', taxItemsDetails);
        },

        async getEditInvoiceDetails(billId) {
            debugger;

            const vm = this;
            vm.isLoading = true;
            try {
                const url = `billId=${billId}`;
                const response = await DataService.GetEditInvoiceDetails(url);

                if (!response?.data?.result) {
                    console.error('Invalid response structure:', response);
                    return;
                }

                const responseData = JSON.parse(response.data.result)?.data;

                if (!Array.isArray(responseData) || responseData.length === 0) {

                    vm.discountDetails = [];
                    vm.taxDetails = [];
                    vm.invoiceItemDetails = [];
                    return;
                }

                const { InvoiceItems, discountsAndtaxes } = responseData[0];

                vm.discountDetails = [];
                vm.taxDetails = [];
                vm.InvoiceFieldsDetails = responseData[0];
                debugger;

                // Handle recurring logic
                if (vm.InvoiceFieldsDetails.is_recurring === 1) {
                    vm.isRecurring = true;
                    const recipients = vm.InvoiceFieldsDetails.to_recipients.split(',');

                    this.recurringItemsDetails = {
                        selectedFrequency: vm.InvoiceFieldsDetails.recurring_frequency,
                        selectedCustomDate: vm.InvoiceFieldsDetails.custom_frequency_date,
                        numberOfRecurrance: vm.InvoiceFieldsDetails.no_of_reoccurence,
                        selectedAutoInvoice: vm.InvoiceFieldsDetails.SendRecurringAutomatically,
                        emails: recipients.map((email, index) => ({
                            id: index + 1,
                            email,
                            errorMessage: ""
                        }))
                    };

                    console.log("recurringItemsDetails", this.recurringItemsDetails);
                } else {
                    vm.isRecurring = false;
                }

                vm.invoiceItemDetails = InvoiceItems.map(element => ({
                    name: element.item_name,
                    description: element.item_description,
                    rate: element.item_unitPrice,
                    units: element.item_Qty,
                    price: element.total
                }));

                discountsAndtaxes?.forEach(element => {
                    const obj = {
                        label: element.AdjName,
                        percentage: element.AdjPercentage,
                        total: element.AdjAmount
                    };

                    if (element.AdjOperator === 'discounts') {
                        vm.discountDetails.push(obj);
                    } else if (element.AdjOperator === 'Taxes') {
                        vm.taxDetails.push(obj);
                    }
                });

            } catch (error) {
                console.error('Error fetching invoice details:', error);
            } finally {
                vm.isLoading = false;
            }
        }



    },

}
</script>
<style>
.heading-border .custom-control.custom-checkbox {
    display: inline;
}
</style>