<template>
  <div class="listing mt-3">
    <div class="table-responsive">
      <Form ref="TaxItemsFields" v-slot="{ valid, errors }">
        <table id="taxAdjustments" class="table table-bordered">
          <thead class="thead-bg">
            <tr>
              <th class="td0" colspan="3" width="80%">{{ $t("Taxes") }}:</th>

              <th class="td3 tdrrm_nd_bold" style="width: 120px">
                <a
                  href="javascript:;"
                  @click="addTaxRow"
                  class="btn btn-primary float-right addDiscount"
                  title="Add More"
                >
                  <em class="fa fa-plus pr-2" aria-hidden="true"></em
                  >{{ $t("Add") }}
                </a>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(tax, index) in taxes"
              :key="tax.id"
              class="ui-state-default"
            >
              <td width="48%" class="td1">
                <Field
                  name="Tax name"
                  :rules="{ required: true }"
                  v-slot="{ errors }"
                >
                  <div>
                    <input
                      type="text"
                      id="label"
                      v-model="tax.label"
                      maxlength="50"
                      placeholder="Enter tax name"
                      v-bind:class="{
                        'txtitems form-control': true,
                        'is-invalid': errors[0],
                      }"
                      @input="searchTaxCategories(tax)"
                    />

                    <ul
                      v-if="showDropdown && filteredTaxList.length"
                      class="dropdown-list"
                    >
                      <li
                        v-for="(item, idx) in filteredTaxList"
                        :key="idx"
                        @click="selectTax(tax, item, index)"
                      >
                        {{ item.name }}
                      </li>
                    </ul>
                  </div>
                </Field>
              </td>

              <td width="20%" class="td2 tdcm">
                <Field
                  name="Tax percentage"
                  :rules="{ required: true }"
                  v-slot="{ errors }"
                >
                  <div class="input-group">
                    <input
                      type="text"
                      id="label"
                      @keydown="validateKey($event)"
                      @input="calculateTax(index)"
                      v-model="tax.percentage"
                      maxlength="50"
                      placeholder="Enter percentage"
                      v-bind:class="{
                        'txtitems form-control': true,
                        'is-invalid': errors[0],
                      }"
                    />
                    <div class="input-group-append discpercentage">
                      <span
                        class="input-group-text"
                        v-text="
                          tax.rate_type && tax.rate_type.toUpperCase() === 'P'
                            ? '%'
                            : 'Flat'
                        "
                      ></span>
                    </div>
                  </div>
                </Field>
              </td>

              <td class="td3" width="20%">
                <Field
                  name="Total"
                  :rules="{ required: false }"
                  v-slot="{ errors }"
                >
                  <div class="input-group">
                    <input
                      type="decimal"
                      v-model="tax.total"
                      readonly
                      class="price_paddright3 form-control invoicebox2_nd"
                      autocomplete="off"
                    />
                  </div>
                </Field>
              </td>

              <td class="td4 single-action text-center" width="8%">
                <a
                  href="javascript:;"
                  @click="removeTaxRow(index)"
                  class="addDiscount text-danger"
                >
                  <em class="fa fa-trash"></em>
                </a>
              </td>
            </tr>
          </tbody>

          <tfoot>
            <tr class="ui-state-default">
              <td></td>

              <td class="td4 arialb000000 text-right align-middle">
                {{ $t("Total") }} {{ $t("Tax") }}
                <label for="lblSubTotalCurrency">{{
                  GetUserInfo.Currency
                }}</label
                >:
              </td>
              <td class="td5 tdcm">
                <input
                  v-model="totalTax"
                  class="form-control pay_field2 number bg-white"
                  type="text"
                  readonly
                />
              </td>
              <td class="td1 tdcm"></td>
            </tr>
            <tr class="ui-state-default">
              <td></td>

              <td class="td4 arialb000000 text-right align-middle">
                {{ $t("Total") }} {{ $t("After") }} {{ $t("Tax") }}
                <label for="lblSubTotalCurrency">{{
                  GetUserInfo.Currency
                }}</label
                >:
              </td>
              <td class="td5 tdcm">
                <input
                  v-model="totalTaxAmount"
                  class="form-control pay_field2 number bg-white"
                  type="text"
                  readonly
                />
              </td>
              <td class="td1 tdcm"></td>
            </tr>
            <!-- 
                        <tr class="title5 title_topbod">
                            <td colspan="2" class="td2 tdcm tdrrm_nd text-right">
                                <strong>Total <label for="lblTotalCurrency"
                                        style="vertical-align: middle">(PKR)</label>:</strong>
                            </td>
                            <td class="td3">
                                <input type="text" v-model="totalTaxAmount"
                                    class="form-control pay_field2 number bg-white" readonly />
                            </td>
                            <td></td>
                        </tr> -->

            <tr class="title5 title_topbod">
              <td colspan="2" class="td2 tdcm tdrrm_nd text-right">
                <strong>
                  {{ $t("DiscountFlatRate") }}
                  <label for="lblDiscountFlatRateCurrency">{{
                    GetUserInfo.Currency
                  }}</label
                  >:</strong
                >
              </td>
              <td class="td3">
                <input
                  v-model="discountFlatRate"
                  type="text"
                  class="form-control invoicebox2_nd"
                  @keydown="validateKey($event)"
                  @input="calculatedTotal"
                />
              </td>
              <td></td>
            </tr>

            <tr class="title5 title_topbod">
              <td colspan="2" class="td2 tdcm tdrrm_nd text-right">
                <strong
                  >{{ $t("AdvanceReceived")
                  }}<label for="lblAdvanceReceivedCurrency">{{
                    GetUserInfo.Currency
                  }}</label
                  >:</strong
                >
              </td>
              <td class="td3">
                <input
                  v-model="advanceReceived"
                  type="text"
                  class="form-control invoicebox2_nd"
                  @keydown="validateKey($event)"
                  @input="calculatedTotal"
                />
              </td>
              <td></td>
            </tr>

            <tr class="title5 title_topbod">
              <td
                colspan="2"
                class="td2 tdcm tdrrm_nd text-right arial16b000000"
              >
                <strong>
                  {{ $t("N etTotal")
                  }}<label for="lblNetTotalCurrency">{{
                    GetUserInfo.Currency
                  }}</label
                  >:</strong
                >
              </td>
              <td class="td3">
                <input
                  type="text"
                  v-model="calculatedTotal"
                  class="form-control pay_field2 number"
                  readonly
                />
              </td>
              <td></td>
            </tr>
          </tfoot>
        </table>
      </Form>
    </div>
  </div>
</template>

<script>
import DataService from "../../../services/DataService";

export default {
  props: {
    discountTotalAmount: {
      type: Number,
    },
    taxDetails: {
      type: Array,
    },
    InvoiceFieldsDetails: {
      type: Object,
    },
  },

  watch: {
    discountTotalAmount(newValue, oldValue) {
      this.totalAmount = newValue;
      this.calTotalAmount = newValue;
      this.applyTaxes();
    },
    taxDetails(newValue, oldValue) {
      this.taxes = newValue;
    },
    InvoiceFieldsDetails(newValue, oldValue) {
      this.discountFlatRate = newValue.DiscountFlatRate;
      this.advanceReceived = newValue.BillAdvanceAmount;
    },
  },

  computed: {
    taxAddUpdate() {
      this.TaxItemsDetails();
      return this.taxes;
    },
    totalTax() {
      return this.taxes
        .reduce((sum, tax) => sum + (parseFloat(tax.total) || 0), 0)
        .toFixed(2);
    },
    totalTaxAmount() {
      const amount = parseFloat(this.totalAmount) || 0;
      return (amount + parseFloat(this.totalTax)).toFixed(2);
    },
    calculatedTotal() {
      const taxAmount = parseFloat(this.totalTaxAmount) || 0;
      const discount = parseFloat(this.discountFlatRate) || 0;
      const advance = parseFloat(this.advanceReceived) || 0;
      let finalTotal = taxAmount - discount - advance;
      if (isNaN(finalTotal)) {
        finalTotal = 0;
      }
      return finalTotal.toFixed(2);
    },
  },
  data() {
    return {
      filteredTaxList: [],
      showDropdown: false,
      TaxCategoryList: [],
      taxes: [{ label: "", percentage: "", total: "", rate_type: "" }],
      discountFlatRate: 0,
      advanceReceived: 0,
      netTotal: 0,
      totalAmount: 0,
      calTotalAmount: 0,
    };
  },

  created() {
    //this.GetTaxCategory();
  },

  methods: {
    GetTaxCategory(searchText = "") {
      debugger;
      var vm = this;
      var url = `id=&moduleName=&type=TAX_CATEGORY&search=${searchText}&refId=`;

      DataService.GetDDLData(url).then((response) => {
        if (response.status === 200 && response.data.DATA) {
          vm.TaxCategoryList = response.data.DATA.map((item) => ({
            value: item.value,
            name: item.name,
            first_value: item.first_value || 0,
            second_value: item.second_value || 0,
          }));

          // Filter frontend as well
          vm.filteredTaxList = vm.TaxCategoryList.filter((t) =>
            t.name.toLowerCase().includes(searchText.toLowerCase())
          );
          vm.showDropdown = true;
        }
      });
    },
    searchTaxCategories(tax) {
      debugger;
      const searchText = tax?.label || "";

      if (searchText.length > 1) {
        this.GetTaxCategory(searchText);
      } else {
        this.showDropdown = false;
        // Reset only the current row
        tax.percentage = 0;
        tax.rate_type = "";
      }
    },

    selectTax(taxRow, selectedItem, index) {
      debugger;
      const exists = this.taxes.some((t) => t.label === selectedItem.name);

      if (!exists) {
        // Update only the selected row's tax name
        taxRow.label = selectedItem.name;
        this.showDropdown = false;

        this.TaxCategoryData = selectedItem.value;

        // Reset only the selected row's percentage and rate_type
        taxRow.percentage = 0; // Reset the selected row's percentage
        taxRow.rate_type = ""; // Reset the selected row's rate_type

        // Fetch the tax details for the selected row
        this.TaxCategoryDetails(taxRow, index);
      } else {
        this.showDropdown = false;
      }
    },

    TaxCategoryDetails(taxRow, index) {
      var ths = this;
      ths.isLoading = true;
      var url = `TaxCategoryId=${ths.TaxCategoryData}&Type=Tax_Category`;

      DataService.GetTaxCategoryDetails(url)
        .then(function (response) {
          ths.isLoading = false;

          // Slab Data
          if (response.data.result.SLABDATA.length === 0) {
            taxRow.RateData = [{ AmountFrom: 0, AmountTo: 0, TaxRate: 0 }];
          } else {
            taxRow.RateData = response.data.result.SLABDATA;
          }

          // Normal DATA
          if (response.data.result.DATA.length > 0) {
            var TaxCategoryData = response.data.result.DATA[0];

            taxRow.rate_type =
              TaxCategoryData.rate_type &&
              ["P", "F"].includes(TaxCategoryData.rate_type.toUpperCase())
                ? TaxCategoryData.rate_type.toUpperCase()
                : "F";

            const subtotal = ths.calTotalAmount;
            const matchingSlab = taxRow.RateData.find(
              (slab) =>
                subtotal >= slab.AmountFrom &&
                (slab.AmountTo === 0 || subtotal <= slab.AmountTo)
            );

            if (matchingSlab) {
              taxRow.percentage = matchingSlab.TaxRate;
              ths.calculateTax(index);
            }
          }
        })
        .catch(function (error) {
          console.error("Error fetching Tax Category details: ", error);
        });
    },

    validateKey(event) {
      const disallowedKeys = ["+", "-", "=", "e"];
      const allowedKeys = [
        "Backspace",
        "Delete",
        "ArrowLeft",
        "ArrowRight",
        "Tab",
        "Enter",
        ".",
      ];
      if (disallowedKeys.includes(event.key)) {
        event.preventDefault();
      }
      if (
        (event.key >= "0" && event.key <= "9") ||
        allowedKeys.includes(event.key)
      ) {
        return;
      }
      event.preventDefault();
    },
    applyTaxes() {
      debugger;
      this.taxes.forEach((tax, index) => {
        this.calculateTax(index);
      });
    },
    addTaxRow(valid, errors) {
      debugger;

      let validationCheck = false;
      this.taxes.forEach((item) => {
        if (item.label == "" && item.percentage == "") {
          validationCheck = true;
          this.$refs.TaxItemsFields.validate();
        }
      });
      if (validationCheck == false) {
        this.$refs.TaxItemsFields.validate()
          .then((result) => {
            if (result) {
              // Add a new row with independent values
              this.taxes.push({
                label: "",
                percentage: 0,
                total: "",
                rate_type: "",
              }); // New row with default values
            } else {
              this.ShowAlert(
                this.$t("Please correct the highlighted errors"),
                "failure",
                this.$t("Alert")
              );
            }
          })
          .catch((error) => {
            console.error("Validation error: ", error);
          });
      } else {
        this.ShowAlert(
          this.$t("Please correct the highlighted errors"),
          "failure",
          this.$t("Alert")
        );
      }
    },

    async removeTaxRow(index) {
      try {
        const result = await new Promise((resolve) => {
          this.confirmR(
            this.$t("Are you sure you want to delete these record(s)?"),
            true,
            false,
            resolve
          );
        });
        if (result == undefined) {
          this.taxes.splice(index, 1);
          this.applyTaxes();
          this.ShowAlert(
            this.$t("Item has been deleted Successfully "),
            "success",
            this.$t("Alert")
          );
        } else {
          //
        }
      } catch (error) {
        console.error("Error during confirmation:", error);
      }
    },
    calculateTax(index) {
      debugger;
      const tax = this.taxes[index];
      const rowAmount = tax.baseAmount || this.calTotalAmount;

      if (!rowAmount) {
        tax.total = 0;
        return;
      }

      if (rowAmount && tax.RateData && tax.RateData.length > 0) {
        const matchingSlab = tax.RateData.find(
          (slab) =>
            rowAmount >= slab.AmountFrom &&
            (slab.AmountTo === 0 || rowAmount <= slab.AmountTo)
        );

        if (matchingSlab) {
          tax.percentage = matchingSlab.TaxRate;
        }
      }

      if (tax.rate_type && tax.rate_type.toUpperCase() === "P") {
        tax.total = (rowAmount * parseFloat(tax.percentage || 0)) / 100;
      } else if (tax.rate_type && tax.rate_type.toUpperCase() === "F") {
        tax.total = parseFloat(tax.percentage || 0);
      } else {
        tax.total = 0;
      }
    },

    TaxItemsDetails() {
      let vm = this;
      let taxes = vm.taxes;
      let totalTax = vm.totalTax;
      let totalTaxAmount = vm.totalTaxAmount;
      let netTotal = this.calculatedTotal;
      let discountFlatRate = vm.discountFlatRate;
      let advanceReceived = vm.advanceReceived;
      let taxItemsDetails = {
        taxes,
        totalTax,
        totalTaxAmount,
        netTotal,
        discountFlatRate,
        advanceReceived,
      };
      this.$emit("TaxItemsDetails", taxItemsDetails);
    },
  },
};
</script>


<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}
</style>